</script>
<style scoped>
    article{ background: #FFF; position: relative; }
    ul{ width: 800px; margin: 0 auto; padding: 40px 0; }
    li a{ position: relative; padding: 20px; margin-top: 40px; box-shadow: 0 0 4px 0 rgba(0,0,0,.2); z-index: 1; border-radius: 5px; }
    li:first-child a{ margin: 0; }
    li.hasbg a{ margin-top: 60px; }
    li.hasbg:first-child a{ margin: 0; }
    i{ position: absolute; top: 0; left: 0; width: 100%; height: 180px; z-index: -99; border-radius: 5px 5px 0 0; }
    header{ font-size: 20px; font-weight: bold; }
    header.ListTitle_Cover{ height: 140px; padding-left: 170px; color: #FFF; }
    header.ListTitle_Cover span{ display: block; padding: 5px 0; text-shadow: 0 2px 4px rgba(0,0,0,.2); font-weight: normal; }
    header.ListTitle_Cover span.InfoTitle{ font-weight: bold; }
    .coverPicture{ position: absolute; left: 20px; top: -20px; width: 140px; height: 180px; box-shadow: 0 4px 8px rgba(0,0,0,0.2); border-radius: 5px; overflow: hidden; background-color: #FFF; display: flex; justify-content: center; }
    .coverPicture img{ height: 100%; }
    p{ font-size: 15px; padding-top: 20px; line-height: 1.8em; overflow: hidden;text-overflow: ellipsis;display: -webkit-box;-webkit-line-clamp: 5;-webkit-box-orient: vertical; }

    @media (max-width: 800px){
        ul{ width: 100%; padding: 40px 15px; box-sizing: border-box; }
        li a{ padding: 15px; }
        header{ font-size: 18px; }
        header.ListTitle_Cover{ height: 120px; padding-left: 120px; }
        header.ListTitle_Cover span{ font-size: 15px; padding: 4px 0; }
        header.ListTitle_Cover span.InfoTitle{ font-size: 18px; }
        .coverPicture{ width: 100px; height: 140px; }
    }

    /*动画*/
    .listanimation-enter-active{
        -webkit-animation: fadeFromBottom 1s both;
        animation: fadeFromBottom 1s both;
    }
</style>



